import React ,{useEffect} from 'react';
import { useState } from 'react';
import {
  Link,
} from 'react-router-dom';


const HomeCategoryCard = (props) => {
  const {
    courseName,
    cardDescription,
    courseBackground,
    coursePath,
  } = props;
  
  const [shortDescription, setShortDescription] = useState('')

  useEffect(() => {
    if(cardDescription) {
      if(cardDescription.length > 85) {
        const trimmedDesc = cardDescription.slice(0, 85) + '...';
        setShortDescription(trimmedDesc);
      }
      else{
        setShortDescription(cardDescription);

      }
    }
  }, [cardDescription])

  return (
    <Link to={`/categories/${coursePath}`} className={'h-96 w-72 shadow-2md  lgmax:first:mt-20 smmax:first:mt-32 lgmax:even:mt-20 lgmax:last:mt-16  hover:-translate-y-1 transition duration-500 ease-in-out   odd:mt-24   rounded-lg bg-white flex flex-col'} >
      <div className="flex flex-col  flex-grow px-3 pt-3 space-y-5">
        <p className={'font-gothic  blockTextP p-3 font-semibold text-2xl'}>{courseName}</p>
        <span className={' font-gothic text-gray-600  text-xl p-3 '}>{shortDescription}</span>
      </div>
      <div className={`w-full h-3/5 ${courseBackground}  bg-cover bg-center rounded-b-lg bg-gray-500  self-end`} >

      </div>
    </Link>
  )
}


export default HomeCategoryCard;